import React, { useState, useRef, useEffect } from "react";
import wrapper from "../../commons/WebComponentWrapper";
import PubSub from 'pubsub-js';
import { getAdditionalAPIs } from "../../commons/ExternalAPIUtil";
import { debounce } from 'lodash';
import {compressToBase64, decompressFromBase64} from "lz-string";
import  axios from 'axios';
import { klona } from "klona";

const dispatchedEvents = [];

/*
    Example: <srs-controller handlers=[{eventName: "XYZ", type: "script", script: `(function(eventPayload) { console.log(eventPayload)})`}]/>
*/
const SrsController = function (props, ref) {

    const findElement = (key, nestedContents) => {
        for (let element of nestedContents) {
            if (element.elementId === key) {
                return element;
            } else if (element.children) {
                const found = findElement(key, element.children);
                if (found) return found;
            }
        }
        return null;
    }

    const findParentElement = (elementId, nestedContents) => {
        const stack = [...nestedContents];

        while (stack.length > 0) {
            const element = stack.pop();

            if (element.elementId === elementId) {
                return null; // The provided elementId is already the top-level element, so no parent exists
            }

            if (element.children) {
                for (let i = 0; i < element.children.length; i++) {
                    const childElement = element.children[i];

                    if (childElement.elementId === elementId) {
                        return element; // Return the current element as the parent if a match is found
                    }

                    if (childElement.children) {
                        stack.push(childElement); // Add the child element to the stack for further iteration
                    }
                }
            }
        }

        return null; // Return null if no parent element is found
    };


    const removeElement = (contents) => {
        return (elementId, _contentItems) => {
            let contentItems = _contentItems || contents || [];
            let parentElement = findParentElement(elementId, contentItems);
            if (parentElement) {
                parentElement.children = parentElement.children.filter((childElement) => childElement.elementId != elementId);
            }
            if (_contentItems) {
                return contentItems;
            } else {
                props.dispatchEvent("PAGE_CONTENTS_REFRESH", { payload: contentItems });
            }
        }
    }

    const getElement = (contents) => {
        return (elementId) => {
            let contentItems = contents ? [...contents] : [];
            return findElement(elementId, contentItems);
        }
    }

    const replaceElement = (elementId, elementObj, contentsToBeUpdated) => {
        contentsToBeUpdated.forEach((element) => {
            if (element.elementId === elementId) {
                element = elementObj;
            } else if (element.children) {
                replaceElement(elementId, elementObj, element.children);
            }
        });
    }

    const setElement = (contents) => {
        return (elementId, elementObj, _contentItems) => {
            let contentItems = _contentItems || contents;
            let contentsToBeUpdated = contentItems ? [...contentItems] : [];
            replaceElement(elementId, elementObj, contentsToBeUpdated);
            if (_contentItems) {
                return contentsToBeUpdated;
            } else {
                props.dispatchEvent("PAGE_CONTENTS_REFRESH", { payload: contentsToBeUpdated });
            }
        }
    }

    const getObjectValue = (obj, path) => {
        const pathArray = Array.isArray(path) ? path : path.split('.');
        let result = obj;
      
        for (const key of pathArray) {
          result = result[key];
      
          if (result === undefined) {
            return undefined;
          }
        }
      
        return result;
    }

    const updateObjectValue = (obj, path, value) => {
        const pathArray = Array.isArray(path) ? path : path.split('.');
        const lastKey = pathArray.pop();
        let target = obj;
      
        for (const key of pathArray) {
          if (typeof target[key] === 'undefined') {
            target[key] = {};
          }
          target = target[key];
        }
      
        if (Array.isArray(target[lastKey])) {
          const index = parseInt(lastKey, 10);
          target[index] = value;
        } else {
          target[lastKey] = value;
        }
    }

    const updateContents = (updatedClan) => {
        props.dispatchEvent("PAGE_CONTENTS_REFRESH", { payload: updatedClan });
    }

    const debouncedProcessEvent = {};

    const processEvent = async (eventName, eventData, handler, functions) => {
        if (handler.type) {
            switch (handler.type) {
                case "script":
                    let evaluateScript = true;
                    const scriptContext = {
                        eventPayload: eventData?.detail || {},
                        context: props.context || {},
                        clan: props.clan || [],
                        dispatchEvent: props.dispatchEvent,
                        helper: {
                            getElement: getElement(props.clan),
                            setElement: setElement(props.clan),
                            removeElement: removeElement(props.clan),
                            findElement,
                            findParentElement,
                            updateContents,
                            compress: compressToBase64,
                            decompress: decompressFromBase64,
                            http: {
                                request: axios.request,
                                get: axios.get,
                                post: axios.post, 
                                put: axios.put, 
                                patch: axios.patch,
                                options: axios.options,
                                head: axios.head, 
                                delete: axios.delete,
                                getUri: axios.getUri
                            },
                            clone: klona,
                            debounce: debounce,
                            ...functions,
                            ...getAdditionalAPIs()
                        }
                    };
                    if(handler.condition) {
                        evaluateScript = false;
                        const conditionFn = new Function("api", `return (${handler.condition})();`);
                        let conditionResult = await conditionFn(scriptContext);
                        if(conditionResult) {
                            evaluateScript = true;
                        }
                    }
                    if(evaluateScript) {
                        const scriptFn = new Function("api", `return (${handler.script})();`);
                        let scriptResult = await scriptFn(scriptContext);
                        // console.log(scriptResult);
                        if (scriptResult) {
                            if (scriptResult.context) {
                                if(props.widgetid && !scriptResult.global) {
                                    props.dispatchEvent("WIDGET_CONTEXT_REFRESH", { payload: scriptResult.context, widgetid: props.widgetid });
                                }else {
                                    props.dispatchEvent("PAGE_CONTEXT_REFRESH", { payload: scriptResult.context });
                                }
                            }
                            if (scriptResult.clan) {
                                if(props.widgetid && !scriptResult.global) {
                                    props.dispatchEvent("WIDGET_CONTENTS_REFRESH", { payload: scriptResult.clan, widgetid: props.widgetid });
                                }else {
                                    props.dispatchEvent("PAGE_CONTENTS_REFRESH", { payload: scriptResult.clan });
                                }
                            }
                        }
                    }
                    break;
                case "relay":
                    let sourceElementId = handler.source_elementid;
                    if(!sourceElementId && props.elementid.endsWith("_ctrl")) {
                        sourceElementId = props.elementid.split("_ctrl")[0];
                    }
                    let targetEvent = handler.target_eventname;
                    let miscEventPayload = handler.add_to_payload || {};

                    if(eventData?.detail?.payload?.id == sourceElementId) {
                        let eventDetails = eventData.detail || {};
                        eventDetails.payload = {...eventDetails.payload, ...miscEventPayload};
                        props.dispatchEvent(targetEvent, eventDetails);
                    }
                    break;
                case "UPDATE_CONTEXT": 
                    let contextPath = handler.contextPath;
                    let contextValueKey = handler.contextValueKey;
                    let elementId = handler.source_elementid;
                    if(!elementId && props.elementid.endsWith("_ctrl")) {
                        elementId = props.elementid.split("_ctrl")[0];
                    }
                    if(elementId && elementId == eventData?.detail?.payload?.id) {
                        let updatedContext = props.context? {...props.context}: {};
                        let eventDetails = eventData?.detail?.payload || {};
                        let contextValue = getObjectValue(eventDetails, contextValueKey);
                        if(contextValue) {
                            updateObjectValue(updatedContext, contextPath, contextValue);
                            if(props.widgetid) {
                                props.dispatchEvent("WIDGET_CONTEXT_REFRESH", { payload: updatedContext, widgetid: props.widgetid });
                            }else {
                                props.dispatchEvent("PAGE_CONTEXT_REFRESH", { payload: updatedContext });
                            }
                        }
                    }
                    break;
                case "UPDATE_GLOBAL_CONTEXT": 
                    let gContextPath = handler.contextPath;
                    let gContextValueKey = handler.contextValueKey;
                    let gElementId = handler.source_elementid;
                    if(!gElementId && props.elementid.endsWith("_ctrl")) {
                        gElementId = props.elementid.split("_ctrl")[0];
                    }
                    if(gElementId && gElementId == eventData?.detail?.payload?.id) {
                        let updatedContext = props.context? {...props.context}: {};
                        let eventDetails = eventData?.detail?.payload || {};
                        let contextValue = getObjectValue(eventDetails, gContextValueKey);
                        if(contextValue) {
                            updateObjectValue(updatedContext, gContextPath, contextValue);
                            props.dispatchEvent("PAGE_CONTEXT_REFRESH", { payload: updatedContext });
                        }
                    }
                    break;
                case "HTTP_REQUEST":
                    let requestConfig = {
                        method: handler.http_method || "get",
                        baseURL: handler.base_url,
                        url: handler.path,
                        headers: handler.headers && typeof handler.headers == "string" && handler.headers != "undefined"? JSON.parse(handler.headers): handler.headers,
                        params: handler.params && typeof handler.params == "string" && handler.params != "undefined"? JSON.parse(handler.params): handler.params,
                        data: handler.data && typeof handler.data == "string" && handler.data != "undefined"? JSON.parse(handler.data): handler.data,
                        responseType: "json",
                        responseEncoding: "utf8"
                    };
                    let result = await axios.request(requestConfig);
                    if(result.data) {
                        let outputMapping = handler.output_mapping && typeof handler.output_mapping == "string" && handler.output_mapping != "undefined"? JSON.parse(handler.output_mapping): handler.output_mapping;
                        if(outputMapping && Object.keys(outputMapping).length > 0) {
                            let updatedContext = props.context? {...props.context}: {};
                            Object.keys(outputMapping).map((resourcePath) => {
                                let responseValue = getObjectValue(result.data, resourcePath);
                                if(responseValue) {
                                    updateObjectValue(updatedContext, outputMapping[resourcePath], responseValue);
                                }
                                if(props.widgetid && !handler.update_global_context) {
                                    props.dispatchEvent("WIDGET_CONTEXT_REFRESH", { payload: updatedContext, widgetid: props.widgetid });
                                    
                                }else {
                                    props.dispatchEvent("PAGE_CONTEXT_REFRESH", { payload: updatedContext });
                                }
                            });
                        }
                    }
                    break;
                case "navigate":
                    break;
            }
        }

    }

    useEffect(() => {
        let functions = {};
        if(props.designtime + "" != "true") { 
            if(props.widgetid) {
                let widgetFunctionPrefix = "$widget_" + props.widgetid + "_";
                if(window.__SRS__?.functions && Object.keys(window.__SRS__.functions)) {
                    Object.keys(window.__SRS__.functions).map((functionName) => {
                        if(functionName.startsWith(widgetFunctionPrefix)) {
                            let widgetFunctionName = functionName.split(widgetFunctionPrefix)[1];
                            functions[widgetFunctionName] = window.__SRS__.functions[functionName];
                        }
                    })
                }
            }else {
                functions = window.__SRS__?.functions || {};
            }

            window.__SRSINIT__ = window.__SRSINIT__ || {};
            window.__SRSINIT__.controllerHandlers = window.__SRSINIT__.controllerHandlers || {};
            let debouncedProcessHandler = window.__SRSINIT__.controllerHandlers;
            
            props.handlers && props.handlers.map((handler) => {
                let debounceHandlerKey = props.elementid + "_" + handler.name;
                if(debouncedProcessHandler[debounceHandlerKey]) {
                    PubSub.unsubscribe(debouncedProcessHandler[debounceHandlerKey]);
                }
                debouncedProcessEvent[debounceHandlerKey] = debouncedProcessEvent[debounceHandlerKey] || debounce(processEvent, 50);
                let eventName = handler.eventName; 
                eventName = (eventName == "onload")? eventName + "_" + props.elementid: eventName;
                debouncedProcessHandler[debounceHandlerKey] = PubSub.subscribe(eventName, async (eventName, eventData) => {
                    let updatedEventData = {...eventData};
                    if(updatedEventData?.detail?.payload) {
                        updatedEventData.detail.payload.widgetId = props.widgetid;
                    }
                    debouncedProcessEvent[debounceHandlerKey](eventName, updatedEventData, handler, functions);
                });
            });
        }

        return () => {
            // if(props.designtime + "" != "true") { 
            //     window.__SRSINIT__ = window.__SRSINIT__ || {};
            //     window.__SRSINIT__.controllerHandlers = window.__SRSINIT__.controllerHandlers || {};
            //     let debouncedProcessHandler = window.__SRSINIT__.controllerHandlers;
            //     props.handlers && props.handlers.map((handler) => {
            //         let debounceHandlerKey = props.widgetid? props.widgetid + "_" + handler.name: props.elementid + "_" + handler.name;
            //         if(debouncedProcessHandler[debounceHandlerKey]) {
            //             PubSub.unsubscribe(debouncedProcessHandler[debounceHandlerKey]);
            //         }
            //     });
            // }
        };
    }, [props]);

    return (<div />);
}

const styles = `
`
const props = [{
    name: "handlers",
    defaultValue: [],
    type: "object"
}]

const slots = [];

const WrappedComponent = wrapper(React.forwardRef(SrsController), props);
export { WrappedComponent, styles, props, dispatchedEvents, slots };